<template>
    <div class="step2">
        <el-col>
            <el-select class="inputs" v-model="value" placeholder="Escolha" :loading="loading">
                <el-option v-for="item in lanches" :label="item.label" :value="item.value" :unit="item.unit"></el-option>
            </el-select>
        </el-col>
        <el-col>
            <el-input-number class="inputs" v-model="qtde":min="1" :max="10"></el-input-number>
        </el-col>
        <el-col>
            <el-button type="success" @click="add">Adicionar</el-button>
        </el-col>
        <el-col>
            <el-table height="150" border :data="tableData" @row-click="apagarItem">
                <el-table-column prop="produto" label="Produto" width="100" align="left">
                </el-table-column>
                <el-table-column prop="qtde" label="Qtde" width="80" align="right">
                </el-table-column>
                <el-table-column prop="unit" label="PU" width="80" align="right">
                </el-table-column>
            </el-table>
        </el-col>
    </div>
</template>
<script>

    import axios from 'axios';

    export default {
        data() {
            return {
                lanches: [{
                        label: 'teste1',
                        value: 'value1',
                        unit: '3,50'
                    },
                    {
                        label: 'teste2',
                        value: 'value2',
                        unit: '1,50'
                    },
                    {
                        label: 'teste3', 
                        value: 'value3',
                        unit: '5,50'
                }],
                tableData: [{
                    produto: 'prod1',
                    qtde: '2',
                    unit: '3,50'
                },
                {
                    produto: 'prod2',
                    qtde: '1',
                    unit: '1,50'
                },
                {
                    produto: 'prod3',
                    qtde: '2',
                    unit: '5,50'
                }],
                value: '',
                qtde: 1,
                loading: false
            }
        },
        mounted() {
            this.request();
        },
        methods: {
            request() {
                axios.post("api/responsavel/lanches")
                    .then(response => {
                        console.log('response');
                        console.log(response);
                    })
                    .catch(error => {
                        console.log('error');
                        console.log(error);
                    })
            },
            add() {
                this.tableData.unshift({
                    produto: this.item.label,
                    qtde: this.qtde,
                    unit: ''
                });
            },
            apagarItem(row, event, column) {
                this.$confirm('Deseja apagar este lanche?', 'Aviso', {
                    confirmButtonText: 'Sim',
                    cancelButtonText: 'Não',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: 'Produto apagado'
                    });
                    const index = this.tableData.indexOf(row);
                    this.tableData.splice(index, 1);
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: 'Produto não apagado'
                    });
                });
            }
        }
    }
</script>
<style scoped>
    .inputs {
        width: 80%;
    }
    .step2 {
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    .step2 > div {
        margin: 15px 0;
    }
</style>


